main {
  height: 100vh;
  justify-content: flex-start;
  align-items: stretch;
  display: flex;
}

.chatList {
  width: 310px;
  background: #fbfbfb;
  flex-grow: 0;
  flex-shrink: 0;
}

.chatList__profile {
  text-align: right;
  color: #999;
  margin-top: 21px;
  margin-right: 10px;
  display: block;
}

.chatList__search {
  margin: 56px 10px 12px;
  position: relative;
  top: 0;
}

.chatList__search input {
  width: 100%;
  text-align: center;
  background: #efefef;
  border-radius: 5px;
  line-height: 34px;
}

.contact {
  box-sizing: border-box;
  height: 71px;
  width: 100%;
  margin-top: 2px;
  position: relative;
}

.contact:hover, .contact.active {
  background-color: #e4edfd;
}

.contact:after {
  content: "";
  height: 1px;
  width: 290px;
  background: #eaeaea;
  margin-left: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.contact__title, .contact__lastMessage, .contact__avatar {
  margin-top: 12px;
  margin-left: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.contact__avatar {
  height: 47px;
  width: 47px;
  background: #eaeaea;
  border-radius: 23.5px;
}

.contact__title, .contact__lastMessage {
  width: 193px;
  margin-left: 67px;
}

.contact__title {
  color: #1e1e1e;
  font-weight: 600;
}

.contact__lastMessage {
  color: #999;
  margin-top: 29px;
}

.contact__lastMessage span {
  color: #1e1e1e;
  font-weight: 500;
}

.contact__time, .contact__unreadCnt {
  margin-right: 10px;
  position: absolute;
  top: 0;
  right: 0;
}

.contact__time {
  color: #999;
  margin-top: 12px;
}

.contact__unreadCnt {
  color: #fff;
  width: 20px;
  height: 20px;
  text-align: center;
  background: #3369f3;
  border-radius: 10px;
  margin-top: 39px;
  font-size: 11px;
  line-height: 20px;
}

.contact__startChat {
  width: 100%;
  height: 100%;
  z-index: 3000;
  margin: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.contact__startChat.active {
  cursor: default;
}

.chatMessageBox-null {
  text-align: center;
  color: #999;
  flex-grow: 1;
  align-self: center;
}

